* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: normal;
}

ul,
li,
ol {
    list-style: none;
}

a {
    text-decoration: none;
    color: inherit;
}

img {
    vertical-align: top;
    border: 0;
    outline: 0;
}

button {
    border: none;
    outline: 0;
    background-color: transparent;
}

input {
    border: none;
    outline: 0;
    background-color: transparent;
}